import {post,get,toLogin} from '@/common/util.js';
import cityData from '@/common/cityname.js';
export default {
	data() {
		return {
			citylist:cityData,
			height:"",
			toitem:'A',
			cityname:"",
			here:"定位中",//当前城市






			
			
		}
	},
	onLoad() {
		let res = uni.getSystemInfoSync()
		this.height = res.windowHeight-uni.upx2px(100);
		var _this=this

		this.hasgps()


		uni.getLocation({
		    type: 'wgs84',
			geocode: true,
		    success: function (res) {





				_this.wxGetCity(res.longitude,res.latitude)

		        // console.log(res);
		    },
			fail:function(){
				_this.here="定位失败"











			}
		});

		//百度定位



	},
	onShow() {

		var _this=this
		uni.getLocation({
		    type: 'wgs84',
			geocode: true,
		    success: function (res) {





				_this.wxGetCity(res.longitude,res.latitude)

		        // console.log(res);
		    },
			fail:function(){
				_this.here="定位失败"











			}
		});

	},
	methods: {
		
		H5getcity(){
			var _this=this
			MP(1).then(BMap => {
				let myCity = new BMap.LocalCity()
				myCity.get(function (res) {
					var cityname=res.name.replace(/市/,'')
					_this.here=cityname;console.log(res,"$$$$$$$")
				})
			})
		},
		//小程序解析经纬度获取城市

		wxGetCity(lon,lat){
			var _this=this
			wx.request({
				url:'https://api.map.baidu.com/reverse_geocoding/v3/?ak=3wwDKCk09o6hU0PK1605QUXOCBqGVHGx&location=' + lat + ',' + lon + '&output=json&coordtype=wgs84ll',
				data: {},
				header: {
					'content-type': 'application/json' // 默认值
				},
				success (res) {
				    // console.log(res)
					var cityname=res.data.result.addressComponent.city.replace(/市/,'')
					_this.here=cityname
				}
			})
		},
		hasgps(){
			uni.getSetting({
			  success: (res) => {
				if (!res.authSetting['scope.userLocation'])
				  wx.showModal({
					content: '检测到您没打开星球客的定位权限，是否去设置打开？',
					confirmText: "确认",
					cancelText: "取消",
					success: function (res) {
					  console.log(res);
					  //点击“确认”时打开设置页面
					  if (res.confirm) {
						console.log('用户点击确认')
						wx.openSetting({
						  success: (res) => { }
						})
					  } else {
						console.log('用户点击取消')
					  }
					}
				  });
			  }
			})
		},

		chooseZM(zm){
			this.toitem=zm
		},
		choosecity(name){
			this.cityname=name;
			uni.setStorageSync('cityname',name)
			uni.showToast({
			  title: '已重新定位城市',
			  success(){
				setTimeout(res=>{
					uni.navigateBack({})
				},1500)
			  }
			})
		},
		choosecity2(name){
			if(name=="定位中"){
				uni.showToast({
				  title: '城市定位中，请稍候再试！',
				  icon:"none"
				})
				return
			}
			if(name=="定位失败"){
				uni.showToast({
				  title: '定位失败，请在设置中打开星球客的位置权限',
				  icon:"none"
				})
				return
			}
			this.cityname=name;
			uni.setStorageSync('cityname',name)
			uni.showToast({
			  title: '已重新定位城市',
			  success(){
				setTimeout(res=>{
					uni.navigateBack({})
				},1500)
			  }
			})
		}
	}
}
